<template>
  <div>
    <div class="pie">
        <div id="pie1">
            <!--  ECharts  DOM -->
            <div id="main1" style="float:left;width:100%;height: 450px"></div>
        </div>
        <div id="pie2">
            <div id="main2" style="float:left;width:100%;height: 450px"></div>
        </div>
    </div>
  </div>
</template>

<script>
    // 
    let echarts = require('echarts/lib/echarts')
    // 
    require('echarts/lib/chart/pie')
    // title
    require('echarts/lib/component/tooltip')
    require('echarts/lib/component/title')


export default {
    created(){
    },
    mounted(){
        this.initData();
    },
    methods:{
        //
        initData() {
        // domecharts
            var myChart = echarts.init(document.getElementById('main1'));
            // 
            myChart.setOption({
                title : {
                    text: '',//
                    // subtext: '',//
                    x:'center',//x
                },
                tooltip : {
                    trigger: 'item',
                    formatter: "{a} <br/>{b} : {c} ({d}%)"
                },
                legend: {
                    orient: 'vertical',
                    bottom: 'bottom',
                    data: ['','','','','']
                },
                series : [
                    {
                        name: '',
                        type: 'pie',
                        radius : '55%',
                        center: ['50%', '60%'],
                        data:[
                            {value:335, name:''},
                            {value:310, name:''},
                            {value:234, name:''},
                            {value:135, name:''},
                            {value:1548, name:''}
                        ],
                        itemStyle: {
                            emphasis: {
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowColor: 'rgba(0, 0, 0, 0.5)'
                            }
                        }
                    }
                ]
            });
        },
    }
}
</script>
<style lang="less">
	
    .pie{
        // display: flex;
        // justify-content: center;
        margin-top: 20px;
    }
</style>







